* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.contact-section {
  padding: 2rem;
  color: rgb(228, 228, 228);
  /* color: black; */
}
.contact-title {
  margin-top: 4rem;
}

.col-2 .address-lists {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.address-icon {
  color: var(--color-3);
  font-size: 20px;
  margin-bottom: 8px;
}
.address-text {
  text-align: center;
  margin-bottom: 35px;
  line-height: 20px;
  padding: 0 30px;
  color: var(--color-4);
}
.email {
  text-align: center;
  margin-bottom: 40px;
}
.social-media-icon {
  width: 30px;
  height: 30px;
  padding: 8px;
  margin: 0 5px;
  cursor: pointer;
  background-color: var(--color-2);
  color: white;
  border-radius: 50%;
}
.social-media {
  width: 180px;
  margin: 0 auto;
}
.contact-form {
  width: 25rem;
  /* border: 2px solid var(--color-3); */
  background-color: var(--color-1);
  box-shadow: 0 0 10px rgb(0, 0, 0, 0.1);
  padding: 30px 20px;
  border-radius: 5px;
  margin: 0 auto;
}

.contact-form .input {
  width: 100%;
  color: var(--color-3);
  background-color: rgb(248, 248, 248);
  outline: none;
  border: 1px solid var(--color-1);
  padding: 10px;
  margin-bottom: 25px;
}
.contact-form textarea {
  resize: none;
}

.input:-webkit-autofill,
.input:-webkit-autofill:hover,
.input:-webkit-autofill:focus,
.input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #eeeeee inset;
  -webkit-text-fill-color: var(--color-4);
}
.contact-submit-btn {
  color: var(--color-1);
  background: var(--color-2);
  outline: none;
  border: none;
  padding: 10px 20px;
  margin-left: auto;
  display: block;
  cursor: pointer;
}

@media only screen and (max-width: 700px) {
  .contact-section {
    padding: 1.8rem 1rem;
  }
  .contact-form {
    width: 16.85rem;
  }
  .social-media {
    margin-bottom: 20px;
  }
}
